#agency {
  position: relative;
  padding: 80px 40px;
  box-sizing: border-box;
  background-color: #ffffff; }
  #agency.show #agency-content .agency-list:nth-child(1) {
    animation: agency-slide-up 0.5s 0s forwards; }
  #agency.show #agency-content .agency-list:nth-child(2) {
    animation: agency-slide-up 0.5s 0.5s forwards; }
  #agency #agency-content {
    max-width: 1000px;
    margin: auto; }
    #agency #agency-content #agency-title {
      font-size: 48px;
      text-align: center; }
      @media only screen and (max-width: 650px) {
        #agency #agency-content #agency-title {
          font-size: 32px; } }
    #agency #agency-content .agency-list {
      display: inline-block;
      width: 100%;
      opacity: 0; }
      #agency #agency-content .agency-list:first-child {
        border-bottom: 1px solid rgba(0, 0, 0, 0.2); }
      #agency #agency-content .agency-list .content-list-title {
        width: 150px;
        font-size: 21px;
        line-height: 100px;
        float: left; }
        @media only screen and (max-width: 650px) {
          #agency #agency-content .agency-list .content-list-title {
            font-size: 18px;
            line-height: 40px;
            float: none; } }
      #agency #agency-content .agency-list .content-list {
        width: auto;
        overflow: hidden; }
        #agency #agency-content .agency-list .content-list .content-list-item {
          display: inline-block;
          position: relative;
          width: 170px;
          height: 100px;
          text-align: center;
          font-size: 19px; }
          @media only screen and (max-width: 650px) {
            #agency #agency-content .agency-list .content-list .content-list-item {
              font-size: 16px; } }
          #agency #agency-content .agency-list .content-list .content-list-item img {
            position: absolute;
            max-width: 100%;
            max-height: 100%;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            transition: 0.3s; }
          #agency #agency-content .agency-list .content-list .content-list-item .content-list-link {
            position: absolute;
            top: 0px;
            left: 0px;
            width: 100%;
            height: 100%;
            background-color: rgba(255, 0, 0, 0.7);
            opacity: 0;
            transition: 0.3s;
            z-index: 1; }
            #agency #agency-content .agency-list .content-list .content-list-item .content-list-link:hover, #agency #agency-content .agency-list .content-list .content-list-item .content-list-link:focus {
              opacity: 1; }
              #agency #agency-content .agency-list .content-list .content-list-item .content-list-link:hover + img, #agency #agency-content .agency-list .content-list .content-list-item .content-list-link:focus + img {
                transform: translate(-50%, -50%) scale(1.2); }
            #agency #agency-content .agency-list .content-list .content-list-item .content-list-link span {
              position: absolute;
              top: 50%;
              left: 50%;
              transform: translate(-50%, -50%);
              font-size: 21px;
              color: #ffffff;
              white-space: nowrap;
              user-select: none; }

@keyframes agency-slide-up {
  from {
    transform: translateY(20px);
    opacity: 0; }
  to {
    transform: translateX(0px);
    opacity: 1; } }
