#about {
  position: relative;
  padding: 80px 40px;
  box-sizing: border-box;
  background-image: linear-gradient(transparent, #ffffff); }
  #about.show #about-info {
    animation: about-slide-right 0.5s 0s forwards; }
  #about.show #about-image {
    animation: about-slide-left 0.5s 0.5s forwards; }
  #about #about-content {
    position: relative;
    max-width: 1000px;
    margin: auto; }
  #about #about-info {
    max-width: 400px;
    padding: 40px;
    margin: 60px 0px;
    background-color: rgba(255, 255, 255, 0.8);
    box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.2);
    border-radius: 5px;
    opacity: 0; }
    #about #about-info #about-title {
      margin: -70px;
      padding: 0px 40px;
      font-size: 48px;
      text-shadow: 1px 1px #ffffff;
      float: left; }
      @media only screen and (max-width: 650px) {
        #about #about-info #about-title {
          margin: 0px 0px 20px 0px;
          font-size: 32px;
          text-align: center;
          float: none; } }
    #about #about-info #about-message {
      font-size: 18px; }
  #about #about-image {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    max-height: 450px;
    opacity: 0;
    border-radius: 5px; }

@keyframes about-slide-right {
  from {
    transform: translateX(-20px);
    opacity: 0; }
  to {
    transform: translateX(0px);
    opacity: 1; } }

@keyframes about-slide-left {
  from {
    transform: translate(-40%, -50%);
    opacity: 0; }
  to {
    transform: translate(-50%, -50%);
    opacity: 1; } }
