#contact {
  position: relative;
  padding: 80px 40px;
  box-sizing: border-box;
  background-color: rgba(255, 255, 255, 0.8); }
  #contact.show #contact-content .contact-item:nth-child(2) {
    animation: contact-slide-up 0.5s 0s forwards; }
  #contact.show #contact-content .contact-item:nth-child(3) {
    animation: contact-slide-up 0.5s 0.5s forwards; }
  #contact.show #contact-content .contact-item:nth-child(4) {
    animation: contact-slide-up 0.5s 1s forwards; }
  #contact #contact-content {
    max-width: 1000px;
    margin: auto; }
    #contact #contact-content #contact-title {
      font-size: 48px;
      text-shadow: 1px 1px #ffffff;
      margin-bottom: 40px; }
      @media only screen and (max-width: 650px) {
        #contact #contact-content #contact-title {
          font-size: 32px; } }
    #contact #contact-content .contact-item {
      opacity: 0; }
      #contact #contact-content .contact-item .contact-subtitle {
        font-size: 24px;
        margin-bottom: 20px; }
      #contact #contact-content .contact-item .contact-info {
        margin-bottom: 40px; }
        #contact #contact-content .contact-item .contact-info td {
          vertical-align: top; }

@keyframes contact-slide-up {
  from {
    transform: translateY(20px);
    opacity: 0; }
  to {
    transform: translateX(0px);
    opacity: 1; } }
